.DApp {
  background-color: aliceblue;;
  padding: 24px 48px;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.DApp > h1 {
  text-align: center;
  color: cornflowerblue;
  margin: 0;
  margin-bottom: 24px;
}

.DApp > h3 {
  text-align: center;
  color: coral;
  margin-top: 24px;
  border-bottom: 2px solid coral;
}

.DApp > button {
  margin-top: 48px;
  width: 100%;
  background-color: cornflowerblue;
  border: none;
  color: #fff;
  padding: 12px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 4px;
  cursor: pointer;
}
