.Field + .Field {
  margin-top: 8px;
}

.Field {
}

.Field > label {
  color:dodgerblue;
  font-size: 16px;
  font-weight: bold;
  min-width: 90px;
  display: inline-block;
  text-align: right;
  font-size: 16px;
  vertical-align: bottom;
}

.Field > label > .Required {
  color: red;
}

.Field > input {
  background: transparent;
  outline: none;
  border: none;
  border-bottom: 1px solid #000;
  margin-left: 12px;
  padding: 8px 12px 0px 0px;
  line-height: 1;
  font-size: 16px;
}